import { Component, OnInit, Input, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Store } from '@ngrx/store';
import * as adminActions from '../store/admin.actions';
import { UserService } from '../services/user.service';
import { Roles, Users } from '../models/user.model';
import { Actions } from '@ngrx/effects';
import { NewsListTemplate } from '../models/admin-news.models';

@Component({
  selector: 'add-news-list-template-dialog',
  templateUrl: './add-news-list-template-dialog.component.html',
  styleUrls: ['./add-news-list-template-dialog.component.css']
})

export class AddNewsListDialogComponent implements OnInit {
  onAdd = new EventEmitter();
  template: NewsListTemplate = new NewsListTemplate();

  constructor(
    public dialogRef: MatDialogRef<AddNewsListDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Users,
    private store: Store<any>,
    private userService: UserService,
    private updates$: Actions,
  ) { }

  ngOnInit() {
    this.template.enabled = true;
  }

  addTemplate() {
    // this.store.dispatch(new adminActions.CreateUser(this.user));
    this.onAdd.emit(this.template);
    this.dialogRef.close(true);
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

}
