import { Action } from '@ngrx/store';
import { News, NewsList, ListOrder } from './../../news/models/news';
import { Users } from '../models/user.model';


export const GET_USERS = '[Admin] GET_USERS';
export const GET_USERS_SUCCESS = '[Admin] GET_USERS_SUCCESS';
export const GET_USERS_ERROR = '[Admin] GET_USERS_ERROR';

export const ASSIGN_ROLE = '[Admin] ASSIGN_ROLE';
export const ASSIGN_ROLE_SUCCESS = '[Admin] ASSIGN_ROLE_SUCCESS';
export const ASSIGN_ROLE_ERROR = '[Admin] ASSIGN_ROLE_ERROR';

export const REMOVE_ROLE = '[Admin] REMOVE_ROLE';
export const REMOVE_ROLE_SUCCESS = '[Admin] REMOVE_ROLE_SUCCESS';
export const REMOVE_ROLE_ERROR = '[Admin] REMOVE_ROLE_ERROR';

export const CREATE_USER = '[Admin] CREATE_USER';
