/**
 * --------------------------------------------------------------------------------------------
 * ------------------------------------------------------------------------------> MISC ACTIONS
 * --------------------------------------------------------------------------------------------
 */
export class LogoutAdminState implements Action {
  readonly type = LOGOUT_ADMIN_STATE;
}

export class UpdateLoaded implements Action {
  readonly type = CHANGE_LOADED;
  constructor(public payload: { isLoaded: boolean }) {}
}

export type All = LogoutAdminState | UpdateLoaded;
