export class Users {
  id?: number;
  name: string;
  email: string;
  created_at?: string;
  updated_at?: string;
  activity?: Activity[];
  roles?: Roles[]
  password?: string;
  profile?: Profile;

  constructor(name, email, password) {
    this.name = name;
    this.email = email;
    this.password = password;
  }
}

export class Roles {
  id: number;
  name: string;
  display_name: string;
  description: string;
  created_at: string;
  updated_at: string;
  pivot: {
      user_id: number,
      role_id: number
  }
  permissions?: Permissions[]

  constructor() {}
}

export class Permissions {
  id: number;
  name: string;
  display_name: string;
  description: string;
  created_at: string;
  updated_at: string;
  pivot: {
      role_id: number,
      permission_id: number
  }

  constructor() {}
}

export class Activity {
  id: number;
  log_name: string;
  description: string;
  subject_id: number;
  subject_type: string;
  causer_id: number;
  causer_type: string;
  properties?: any[];
  created_at: string;
  updated_at: string;

  constructor() {}
}

export class Profile{
  id?: string;
  user_id?: string;
  dob?: string;
  gender?: string;
  fb?: string;
  insta?: string;
  twitter?: string;
  email?: string;
  phone?: string;
  profilepic?: string;
  coverimage?: string;

  constructor(){
    this.user_id    = '-';
    this.dob        = '-';
    this.gender     = '-';
    this.fb         = '-';
    this.insta      = '-';
    this.twitter    = '-';
    this.email      = '-';
    this.phone      = '-';
    this.profilepic = '';
    this.coverimage = '';

  }
}
