import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { ConfirmDialogComponent } from '../../shared/component/confirm-dialog/confirm-dialog.component';
import { NewsService } from '../services/news.service';
import { News } from '../../news/models/news';
import { AddNewsBreakerDialogComponent } from '../add-news-breaker-dialog/add-news-breaker-dialog.component';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-news-breakers',
  templateUrl: './news-breakers.component.html',
  styleUrls: ['./news-breakers.component.css']
})
export class NewsBreakersComponent implements OnInit, OnDestroy {
  breakers: News[];
  destroySubject$: Subject<void> = new Subject();

  constructor(
    private adminNewsService: NewsService,
    public dialog: MatDialog,
    public snackBar: MatSnackBar
  ) { }

  ngOnInit() {
    this.adminNewsService.getBreakers().subscribe(breakers => {
      this.breakers = breakers
    });
  }

  onAddBreaker() {
    let dialogRef = this.dialog.open(AddNewsBreakerDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%' });

    dialogRef.componentInstance.onAdd
    .pipe(takeUntil(this.destroySubject$))
    .subscribe((result: News) => {
      this.adminNewsService.createBreaker(result).subscribe((data: News[]) => {
        this.breakers = data;
      });
    });
  }

  onDeleteBreaker(id: number){
    let dialogRef = this.dialog.open(ConfirmDialogComponent,
      { width: '350px', data: {title: 'Delete News Breaker', message: 'Are you sure you want to delete this Breaker?'} });

      dialogRef.afterClosed()
      .pipe(takeUntil(this.destroySubject$))
      .subscribe((result: string) => {
        dialogRef = null;
        if (result) {
          this.adminNewsService.deleteBreakers(id).subscribe((data: News[]) => {
            this.breakers = data;
          });
        }
      });
  }

  ngOnDestroy() {
    this.destroySubject$.next();
  }

}
