import { Component, OnInit, OnDestroy } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';

import { Users } from '../models/user.model';

import { Store } from '@ngrx/store';
import {Actions, ofType} from '@ngrx/effects';

import * as AdminActions from '../store/admin.actions';
import { AssignRoleDialogComponent } from '../assign-role-dialog/assign-role-dialog.component';
import { AddUserDialogComponent } from '../add-user-dialog/add-user-dialog.component';
import { SessionService } from '../../shared/services/session.service';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit, OnDestroy {

  sidePanelOpened = true;
  users: Users[] = [];
  selectedUser: Users = null;
  currentUserEmail: string;
  destroySubject$: Subject<void> = new Subject();

  updateUser: Users = null;

  public storagePath: string;

  constructor(
    private store: Store<any>,
    private updates$: Actions,
    public dialog: MatDialog,
    private sessionService: SessionService,
    public snackBar: MatSnackBar
  ) { }

  ngOnInit() {
    this.storagePath = environment.storageUrl;
    this.store.dispatch(new AdminActions.GetUsers());

    // update selected list selection
    this.updates$
    .pipe(
      ofType(AdminActions.GET_USERS_SUCCESS),
      takeUntil(this.destroySubject$)
    )
    .subscribe(() => {
      let sub = this.store.select(adminStore => adminStore.Admin.users)
      .pipe(takeUntil(this.destroySubject$))
      .subscribe(userList => {
        this.users = userList;
      });
    });

    //get current user email
    this.store.select(store => store.UserState.user.email)
    //.take(1)
    .pipe(takeUntil(this.destroySubject$))
    .subscribe(email => this.currentUserEmail = email);
  }

  onAddRole() {
    let dialogRef = this.dialog.open(AssignRoleDialogComponent, { width: '95%', height: '60%', maxHeight: '95%', data: this.selectedUser });

    dialogRef.componentInstance.onAdd.subscribe((result: any) => {
      this.updates$
      .pipe(
        ofType(AdminActions.ASSIGN_ROLE_SUCCESS),
        takeUntil(this.destroySubject$)
      )
      .subscribe(() => {

        this.openSnackBar('Role Added to User!');

        this.store.select(store => store.Admin.users)
        .pipe(takeUntil(this.destroySubject$))
        .subscribe(users => {
          users.forEach((user: Users) => {
            if (user.id === result.user_id) {
              this.selectedUser = user;
            }
          });
        });

      });
    });
  }


  onRemoveRole(roleid: number, userid: number) {
    this.store.dispatch(new AdminActions.RemoveRole({ user_id: this.selectedUser.id, role_id: roleid }));

    this.updates$
    .pipe(
      ofType(AdminActions.REMOVE_ROLE_SUCCESS),
      takeUntil(this.destroySubject$)
    )
    .subscribe(() => {

      this.openSnackBar('Role Removed from User!');

      this.store.select(store => store.Admin.users)
      .pipe(takeUntil(this.destroySubject$))
      .subscribe(users => {
        users.forEach((user: Users) => {
          if (user.id === userid) {
            this.selectedUser = user;
          }
        });
      });

    });
  }

  onAddUser() {
    let dialogRef = this.dialog.open(AddUserDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%' });

    dialogRef.componentInstance.onAdd.pipe(takeUntil(this.destroySubject$)).subscribe((result: any) => {
      this.updates$
      .pipe(
        ofType(AdminActions.CREATE_USER_SUCCESS),
        takeUntil(this.destroySubject$)
      )
      .subscribe(() => {

        this.openSnackBar('User Created!');

        // this.store.select(store => store.Admin.users)
        // .subscribe(users => {
        //   users.forEach((user: Users) => {
        //     if (user.id === this.selectedUser.id) {
        //       this.selectedUser = user;
        //     }
        //   });
        // });

      });
    });
  }


  checkRight(role: string, user: Users) {
    let re = false;
    user.roles.forEach(urole => {
      if(urole.name === role){
        re = true;
      }
    });

    return re;
  }

  isOver(): boolean {
    return window.matchMedia(`(max-width: 960px)`).matches;
  }

  onSelect(user: Users): void {
    this.selectedUser = user;

    this.updateUser = new Users(this.selectedUser.name, this.selectedUser.email, null);
    this.updateUser.id = this.selectedUser.id;
  }

  onUpdateProfile() {
    this.store.dispatch(new AdminActions.UpdateUser(this.updateUser));

    // update selected list selection
    this.updates$
    .pipe(
      ofType(AdminActions.UPDATE_USER_SUCCESS),
      takeUntil(this.destroySubject$)
    )
    .subscribe(() => {
      //show notification
      this.openSnackBar('User Data Updated!');

      let sub = this.store.select(adminStore => adminStore.Admin.users)
      .subscribe(userList => {
        this.users = userList;
      });
    });
  }

  openSnackBar(message: string, action: string= 'close') {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }

  ngOnDestroy() {
    this.destroySubject$.next();
  }


  checkMyRight(right?: string) {
    if (this.sessionService.checkRole('ticker-editor')) {
      return true;
    } else if (this.sessionService.checkRole('news-editor')) {
      return true;
    }
    return false;
  }
}
