import { Component, OnInit, OnDestroy, Input, Output, EventEmitter, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { Store } from '@ngrx/store';
import * as adminActions from '../store/admin.actions';
import { UserService } from '../services/user.service';
import { Roles, Users } from '../models/user.model';
import {Actions, ofType} from '@ngrx/effects';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'assign-role-dialog',
  templateUrl: './assign-role-dialog.component.html',
  styleUrls: ['./assign-role-dialog.component.css']
})
export class AssignRoleDialogComponent implements OnInit, OnDestroy {
  onAdd = new EventEmitter();
  roles: Roles[] = [];
  destroySubject$: Subject<void> = new Subject();

  constructor(
    public dialogRef: MatDialogRef<AssignRoleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Users,
    private store: Store<any>,
    private userService: UserService,
    private updates$: Actions,
  ) { }

  ngOnInit() {
    this.userService.getRoles().subscribe(roles => {
      this.roles = roles;
    });
  }

  userHasRole(role_: string) {
    let re = false;

    this.data.roles.forEach((element: Roles) => {
      if(element.name === role_) {
        re = true;
        //console.log(element.name, role_)
      }
    });

    return re;
  }

  assignRole(roleid: number) {
    this.store.dispatch(new adminActions.AssignRole({ user_id: this.data.id, role_id: roleid }));
    this.onAdd.emit({ user_id: this.data.id });

    this.updates$
      .pipe(
        ofType(adminActions.ASSIGN_ROLE_SUCCESS),
        takeUntil(this.destroySubject$)
      )
      .subscribe(() => {

        this.store.select(store => store.Admin.users)
        .subscribe(users => {
          users.forEach((user: Users) => {
            if (user.id === this.data.id) {
              // console.log(element)
              this.data = user;
            }
          });
        });

      });
  }

  closeDialog(confirmation: boolean) {
    this.dialogRef.close(confirmation);
  }

  doAdd() {
    this.closeDialog(true);
    this.onAdd.emit();
  }

  ngOnDestroy() {
    this.destroySubject$.next();
  }

}
