import { News } from '../../news/models/news';
import { Action, createFeatureSelector, createSelector } from '@ngrx/store';

import * as AdminActions from './admin.actions';
import { ReviewNewsState, AdminState } from '../../store/app.state';





export const initialState: AdminState = {
  isLoaded: false,
  users: []
};

export type Actions = AdminActions.All;
export interface ActionWithPayload<T> extends Action {
  payload: T;
}

export function reducer(state = initialState, action: ActionWithPayload<any>): AdminState {
  switch (action.type) {

    /**
     * ---------------------------------------------------------------------------------------------------------------
     * --------------------------------------------------------------------------------------> USER MANAGEMENT ACTIONS
     * ---------------------------------------------------------------------------------------------------------------
     */
    /**
     * --------------------------------------------------------------------------------------> GET USERS
     */
    case AdminActions.GET_USERS: {
      return {
        ...state,
        isLoaded: false
      }
    }

    case AdminActions.GET_USERS_SUCCESS: {
      return {
        ...state,
        users: action.payload,
        isLoaded: true
      }
    }

    /**
     * --------------------------------------------------------------------------------------> ASSIGN ROLE
     */
    case AdminActions.ASSIGN_ROLE: {
      return {
        ...state,
        isLoaded: false
      }
    }

    case AdminActions.ASSIGN_ROLE_SUCCESS: {
      return {
        ...state,
        users: action.payload,
        isLoaded: true
      }
    }

    /**
     * --------------------------------------------------------------------------------------> REMOVE ROLE
     */
    case AdminActions.REMOVE_ROLE: {
      return {
        ...state,
        isLoaded: false
      }
    }

    case AdminActions.REMOVE_ROLE_SUCCESS: {
      return {
        ...state,
        users: action.payload,
        isLoaded: true
      }
    }

    /**
     * --------------------------------------------------------------------------------------> CREATE USER
     */
    case AdminActions.CREATE_USER: {
      return {
        ...state,
        isLoaded: false
      }
    }

    case AdminActions.CREATE_USER_SUCCESS: {
      return {
        ...state,
        users: action.payload,
        isLoaded: true
      }
    }

    /**
     * --------------------------------------------------------------------------------------> UPDATE USER
     */
    case AdminActions.UPDATE_USER: {
      return {
        ...state,
        isLoaded: false
      }
    }

    case AdminActions.UPDATE_USER_SUCCESS: {
      return {
        ...state,
        users: action.payload,
        isLoaded: true
      }
    }



    case AdminActions.GET_USERS_ERROR: {
      return {
        ...state,
        isLoaded: true
      }
    }

    default: {
      return state;
    }
  }
}

export const getAdminState = createFeatureSelector<AdminState>('Admin');
