
import {catchError, mergeMap,  map } from 'rxjs/operators';
import { Injectable } from '@angular/core';
import { Observable ,  of } from 'rxjs';
import { Action } from '@ngrx/store';




import {Actions, Effect, ofType} from '@ngrx/effects';

import * as AdminActions from './admin.actions';
import { News } from '../../news/models/news';
import { UserService } from '../services/user.service';

@Injectable()
export class AdminEffects {

  constructor(
    private actions$: Actions,
    private userService: UserService
  ) { }

  //Effects go here
  @Effect()
  GetMyNews$: Observable<Action> = this.actions$.
    pipe(
      ofType<AdminActions.GetUsers>(AdminActions.GET_USERS),
      mergeMap(action =>
        this.userService.getUsers().pipe(
        map((data) => {
          //console.log(data);
          return new AdminActions.GetUsersSuccess(data)
        }),
        catchError(() => of(new AdminActions.GetUsersError())),)
      )
    );

  @Effect()
  AssignRole$: Observable<Action> = this.actions$.
    pipe(
      ofType<AdminActions.AssignRole>(AdminActions.ASSIGN_ROLE),
      mergeMap(action =>
        this.userService.assignRole({ user_id: action.payload.user_id, role_id: action.payload.role_id }).pipe(
        map((data) => {
          console.log(data);
          return new AdminActions.AssignRoleSuccess(data)
        }),
        catchError(() => of(new AdminActions.AssignRoleError())),)
      )
    );

  @Effect()
  RemoveRole$: Observable<Action> = this.actions$.
    pipe(
      ofType<AdminActions.RemoveRole>(AdminActions.REMOVE_ROLE),
      mergeMap(action =>
        this.userService.removeRole({ user_id: action.payload.user_id, role_id: action.payload.role_id }).pipe(
        map((data) => {
          console.log(data);
          return new AdminActions.RemoveRoleSuccess(data)
        }),
        catchError(() => of(new AdminActions.RemovenRoleError())),)
      )
    );

  @Effect()
  CreateUser$: Observable<Action> = this.actions$.
    pipe(
      ofType<AdminActions.CreateUser>(AdminActions.CREATE_USER),
      mergeMap(action =>
        this.userService.createUser(action.payload).pipe(
        map((data) => {
          console.log(data);
          return new AdminActions.CreateUserSuccess(data)
        }),
        catchError(() => of(new AdminActions.CreateUserError())),)
      )
    );

  @Effect()
  UpdateUser$: Observable<Action> = this.actions$.
    pipe(
      ofType<AdminActions.CreateUser>(AdminActions.UPDATE_USER),
      mergeMap(action =>
        this.userService.updateUser(action.payload).pipe(
        map((data) => {
          console.log(data);
          return new AdminActions.UpdateUserSuccess(data)
        }),
        catchError(() => of(new AdminActions.UpdateUserError())),)
      )
    );

}
