import { Component, OnInit, OnDestroy } from '@angular/core';
import { NewsService } from '../services/news.service';
import { NewsListTemplate, SettingsKey } from '../models/admin-news.models';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { AddNewsListDialogComponent } from '../add-news-list-template-dialog/add-news-list-template-dialog.component';
import { ConfirmDialogComponent } from '../../shared/component/confirm-dialog/confirm-dialog.component';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-news-list',
  templateUrl: './news-list.component.html',
  styleUrls: ['./news-list.component.css']
})
export class NewsListComponent implements OnInit, OnDestroy {
  templates: NewsListTemplate[] = [];
  generationTime: SettingsKey = new SettingsKey();
  destroySubject$: Subject<void> = new Subject();

  constructor(
    private adminNewsService: NewsService,
    public dialog: MatDialog,
    public snackBar: MatSnackBar
  ) { }

  ngOnInit() {
    this.generationTime.key = 'list_generation_time';

    this.adminNewsService.getListTemplates().pipe(takeUntil(this.destroySubject$)).subscribe((templates: NewsListTemplate[]) => this.templates = templates);
    this.adminNewsService.getListGenTime().pipe(takeUntil(this.destroySubject$)).subscribe((time: SettingsKey) => this.generationTime.value = time.value);
  }

  enable(id: number) {
    this.adminNewsService.enableListTemplate({template_id: id}).pipe(takeUntil(this.destroySubject$)).subscribe(data => {
      this.templates = data;
    });
  }

  disable(id: number) {
    this.adminNewsService.disableListTemplate({template_id: id}).pipe(takeUntil(this.destroySubject$)).subscribe(data => {
      this.templates = data;
    });
  }

  updateTime() {
    this.adminNewsService.setSettingsKey(this.generationTime).pipe(takeUntil(this.destroySubject$)).subscribe(data => {
      this.generationTime = data;
      this.openSnackBar('List Generations Time updated!');
    });
  }

  onAddTemplate() {
    let dialogRef = this.dialog.open(AddNewsListDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%' });

    dialogRef.componentInstance.onAdd.pipe(takeUntil(this.destroySubject$)).subscribe((result: NewsListTemplate) => {
      this.adminNewsService.createListTemplate(result).pipe(takeUntil(this.destroySubject$)).subscribe((data: NewsListTemplate[]) => {
        this.templates = data;
      });
    });
  }

  onDeleteTemplate(id: number) {
    let dialogRef = this.dialog.open(ConfirmDialogComponent,
      { width: '350px', data: {title: 'Delete News List Template', message: 'Are you sure you want to delete this Template?'} });

      dialogRef.afterClosed().pipe(takeUntil(this.destroySubject$)).subscribe((result: string) => {
        dialogRef = null;
        if (result) {
          this.adminNewsService.deleteListTemplate(id).pipe(takeUntil(this.destroySubject$)).subscribe((data: NewsListTemplate[]) => {
            this.templates = data;
          });
        }
      });
  }


  openSnackBar(message: string, action: string= 'close') {
    this.snackBar.open(message, action, {
      duration: 1000,
    });
  }

  ngOnDestroy() {
    this.destroySubject$.next();
  }

}
