import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { environment } from './../../../environments/environment';
import { News, ListOrder } from '../../news/models/news';
import { Users } from '../models/user.model';
import { NewsListTemplate, SettingsKey } from '../models/admin-news.models';

@Injectable({
  providedIn: 'root'
})
export class NewsService {

  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }


  // get list templates
  getListTemplates () {
    return this.http.get<any>(this.apiUrl + 'management-console/news/list-templates');
  }

  // get list generation time
  getListGenTime () {
    return this.http.get<any>(this.apiUrl + 'management-console/news/list-templates/time');
  }

  // enable news list template
  enableListTemplate (body: { template_id: number }) {
    return this.http.put<any>(this.apiUrl + 'management-console/news/list-templates/enable', body);
  }

  // enable news list template
  disableListTemplate (body: { template_id: number }) {
    return this.http.put<any>(this.apiUrl + 'management-console/news/list-templates/disable', body);
  }

  // set value for a settings key
  setSettingsKey (body: SettingsKey) {
    return this.http.put<any>(this.apiUrl + 'management-console/settings/set', body);
  }

  // create a news list template
  createListTemplate (template: NewsListTemplate) {
    return this.http.post<any>(this.apiUrl + 'management-console/news/list-templates/create', template);
  }

  // delete a news list template
  deleteListTemplate (id: number) {
    return this.http.delete<any>(this.apiUrl + 'management-console/news/list-templates/'+id);
  }


  /**---------------------------------------------------------------------------------
   * ------------------------------------------------------------------> News Breakers
   ---------------------------------------------------------------------------------*/
  // get breakers
  getBreakers () {
    return this.http.get<any>(this.apiUrl + 'management-console/news/breakers/manage/get');
  }

  // create a news breaker
  createBreaker (template: News) {
    return this.http.post<any>(this.apiUrl + 'management-console/news/breakers/manage/create', template);
  }

  // delete a news breaker
  deleteBreakers (id: number) {
    return this.http.delete<any>(this.apiUrl + 'management-console/news/breakers/manage/delete/'+id);
  }
}
