import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { environment } from './../../../environments/environment';
import { News, ListOrder } from '../../news/models/news';
import { Users } from '../models/user.model';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }


  // get selected list
  getUsers () {
    return this.http.get<any>(this.apiUrl + 'management-console/users');
  }

  // get all roles for assignment
  getRoles () {
    return this.http.get<any>(this.apiUrl + 'management-console/user/get-roles');
  }

  // assign role to user
  assignRole ( user_role: { user_id: number, role_id: number } ) {
    return this.http.put<any>(this.apiUrl + 'management-console/user/assign-role', user_role);
  }

  // remove role from user
  removeRole ( user_role: { user_id: number, role_id: number } ) {
    return this.http.put<any>(this.apiUrl + 'management-console/user/remove-role', user_role);
  }

  // create user
  createUser ( user: Users ) {
    return this.http.post<any>(this.apiUrl + 'management-console/user/create', user);
  }

  // update user
  updateUser ( user: Users ) {
    return this.http.put<any>(this.apiUrl + 'management-console/user/update', user);
  }
}
